/* variables
============================================== */

@import "imports/variables.less";

/* mixins
======================================== */

@import "imports/_mixins";

/* fonts
=============================================== */

@import "imports/fonts.less";

/* elements
================================ */

@import "imports/elements.less";

/* bootstrap
=============================================*/
@import "../../bower_components/bootstrap/less/bootstrap.less";

/* base
======================================== */

@import "imports/base.less";

/* pages
========================================= */
.menu-top{
    padding: 30px 0;
    background: @white;
    &__left{
        float: left;
        min-width: 620px;
        li{
            margin-right: 30px;
        }
    }
    &__right{
        float: right;
        min-width: 350px;
        li{
            margin-left: 30px;
        }
    }
    li{
        .inline-block(@valign: middle);
    }
    img{
        margin-top: 4px;
        margin-right: 2px;
    }
}

/* menu 
===================================*/
.fa-caret-down{
    .inline-block;
    margin-top: -4px;
    padding: 5px;
    color: #d1d1d1;
}
.dropdown{
    position: relative;
}
.dropdown-menu{
    position: absolute;
    left: 0;
    display: none;
    padding: 20px;
    background: @white;
    box-shadow: 0 3px 5px rgba(0,0,0,.2);
    z-index: 10;
    li{
        display: block;
        margin: 0;
        margin-bottom: 20px;
        .line-height(20px);
    }
}
.fa-shopping-cart{
    margin-right: 4px;
}

/*   button show-menu
============================*/
.show-menu{
    position: relative;
    display: none;
    width: 100%;
    height: 40px;
    background: @white;
    border: none;
    outline: none;
    cursor: pointer;
}
.fa-bars{
    float: right;
    margin-right: 40px;
}



/* media
================================== */


/* Large Devices, Wide Screens */

@media only screen and (max-width: 1200px) {
    .menu-top{
        &__left{
            float: none;
            margin-bottom: 20px;
        }
        &__right{
            float: none;
            li{
                margin-left: 0;
            }
        }
    }

}


/* Medium Devices, Desktops */

@media only screen and (max-width: 992px) {
   
}


/* Small Devices, Tablets */

@media only screen and (max-width: 767px) {

}

@media only screen and (max-width: 768px) {
    .show-menu{
        display: block;
        li{
            text-align: center;
        }
    }
    .menu-top{
        display: none;
        &__left{
            margin-bottom: 0;
        }
        li{
            display: block;
            margin-bottom: 20px;
        }
        a{
            text-align: center;
        }
    }
}


/* Extra Small Devices, Phones */

@media only screen and (max-width: 480px) {

