/* base
======================================== */
* {
  padding: 0;
  margin: 0;
}
body {
  min-width: 320px;
  font-family: 'OpenSans', sans-serif;
  font-size: 14px;
  line-height: 20px;
  color: #000;
  background: #ececec;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  font-family: 'OpenSans', sans-serif;
  font-size: 14px;
  color: #000;
  text-transform: uppercase;
  text-decoration: none;
}
a:hover {
  color: #999999;
}
h3 {
  font-size: 24px;
  font-weight: bold;
}
ul {
  list-style: none;
}
/* pages
========================================= */
.menu-top {
  padding: 30px 0;
  background: #fff;
}
.menu-top__left {
  float: left;
  min-width: 620px;
}
.menu-top__left li {
  margin-right: 30px;
}
.menu-top__right {
  float: right;
  min-width: 350px;
}
.menu-top__right li {
  margin-left: 30px;
}
.menu-top li {
  display: inline-block;
  vertical-align: middle;
}
.menu-top img {
  margin-top: 4px;
  margin-right: 2px;
}
/* menu 
===================================*/
.fa-caret-down {
  display: inline-block;
  vertical-align: top;
  margin-top: -4px;
  padding: 5px;
  color: #d1d1d1;
}
.dropdown {
  position: relative;
}
.dropdown-menu {
  position: absolute;
  left: 0;
  display: none;
  padding: 20px;
  background: #fff;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.2);
  z-index: 10;
}
.dropdown-menu li {
  display: block;
  margin: 0;
  margin-bottom: 20px;
  height: 20px;
  line-height: 20px;
}
.fa-shopping-cart {
  margin-right: 4px;
}
/*   button show-menu
============================*/
.show-menu {
  position: relative;
  display: none;
  width: 100%;
  height: 40px;
  background: #fff;
  border: none;
  outline: none;
  cursor: pointer;
}
.fa-bars {
  float: right;
  margin-right: 40px;
}
/* media
================================== */
/* Large Devices, Wide Screens */
@media only screen and (max-width: 1200px) {
  .menu-top__left {
    float: none;
    margin-bottom: 20px;
  }
  .menu-top__right {
    float: none;
  }
  .menu-top__right li {
    margin-left: 0;
  }
}
/* Medium Devices, Desktops */
/* Small Devices, Tablets */
@media only screen and (max-width: 768px) {
  .show-menu {
    display: block;
  }
  .show-menu li {
    text-align: center;
  }
  .menu-top {
    display: none;
  }
  .menu-top__left {
    margin-bottom: 0;
  }
  .menu-top li {
    display: block;
    margin-bottom: 20px;
  }
  .menu-top a {
    text-align: center;
  }
}
/* Extra Small Devices, Phones */
